import React, {useEffect, useState} from 'react';
import {connect} from "react-redux";

import {useLocation} from "react-router-dom";

import Header from "./header"

import {setClose, setOpen} from "../../redux-toolkit/nav/nav-slice";

const HeaderContainer = ({setOpen, setClose, isOpen}) => {
    const location = useLocation()

    const [titlePage, setTitlePage] = useState("/")

    useEffect(() => {
        const titles_page = {
            "/": "",
            "/schedule": "",
            "/journal": "",
            "/bactericidal-lamp": "",
            "/students": "",
            "/teachers": ""
        }

        setTitlePage(titles_page[location.pathname])
    }, [location])

    return <Header titlePage={titlePage} setClose={setClose} setOpen={setOpen} isOpen={isOpen}/>
}

const mapStateToProps = (state) => {
    return {
        isOpen: state.navSliceReducer.isOpen
    }
}

const mapDispatchToProps = {
    setClose,
    setOpen
}

export default connect(mapStateToProps, mapDispatchToProps)(HeaderContainer);
