import React, {useEffect, useState} from "react";
import {useLocation} from "react-router-dom";
import {useMediaQuery} from "react-responsive";
import {connect} from "react-redux";

import App from "./app";

import appSCSS from "./app.module.scss";
import mainSCSS from "../pages/main/mainSCSS.module.scss";
import scheduleSCSS from "../pages/schedule/scheduleSCSS.module.scss";
import currentWeekSCSS from "../components/current week/list/current-weekSCSS.module.scss";
import {ResizeSensor} from "css-element-queries";

function AppContainer({pages}) {
    const location = useLocation();
    const [windowInnerClientHeight, setWindowInnerClientHeight] = useState(null);
    const [windowInnerClientWidth, setWindowInnerClientWidth] = useState(null);

    const is1023 = useMediaQuery({query: `(max-width: 1023px)`});
    const is767 = useMediaQuery({query: `(max-width: 767px)`});

    const handleResizeContent = (windowInnerClientHeight) => {
        const app_content = document.getElementsByClassName(`${appSCSS.content}`)[0];
        const app_content_margin_top = +getComputedStyle(app_content).marginTop.replace("px", "");
        app_content.style.height = `${windowInnerClientHeight - (app_content_margin_top + ((is767 && 15) || (is1023 && 20) || ((!is1023 && !is767) && 30)))}px`;

        let wrap_first_scss = null;
        let wrap_second_scss = null;

        if (pages.main === location.pathname) {
            wrap_first_scss = mainSCSS.wrap_first
            wrap_second_scss = mainSCSS.wrap_second
        } else if (pages.schedule === location.pathname) {
            wrap_first_scss = scheduleSCSS.wrap_first
            wrap_second_scss = scheduleSCSS.wrap_second
        }

        const wrap_first_height = document.getElementsByClassName(`${wrap_first_scss}`)[0]?.clientHeight
        const wrap_second = document.querySelector(`div.${wrap_second_scss}`)
        if (wrap_second !== null) {
            wrap_second.style.height = `${windowInnerClientHeight - (app_content_margin_top + wrap_first_height + ((is767 && 15) || (is1023 && 20) || ((!is1023 && !is767) && 30)) + 2)}px`
        }
    }

    useEffect(() => {
        handleResizeContent(document.documentElement?.clientHeight)

        if (pages.schedule === location.pathname) {
            const current_week_calendar = document.getElementsByClassName(`${currentWeekSCSS.calendar}`)[0];
            new ResizeSensor(current_week_calendar, () => {
                handleResizeContent(document.documentElement?.clientHeight)
            })
        }
        return () => {
            if (pages.schedule === location.pathname) {
                const current_week_calendar = document.getElementsByClassName(`${currentWeekSCSS.calendar}`)[0];
                ResizeSensor.detach(current_week_calendar)
            }
        }
    }, [windowInnerClientHeight, windowInnerClientWidth, location])

    useEffect(() => {
        window.addEventListener("resize", () => setWindowInnerClientHeight(document.documentElement?.clientHeight))
        window.addEventListener("resize", () => setWindowInnerClientWidth(document.documentElement?.clientWidth))
        return () => {
            window.removeEventListener("resize", () => setWindowInnerClientHeight(document.documentElement?.clientHeight))
            window.removeEventListener("resize", () => setWindowInnerClientWidth(document.documentElement?.clientWidth))
        }
    }, [])

    return <App/>
}

const mapStateToProps = (state) => {
    return {
        pages: state.navSliceReducer.pages
    }
}

export default connect(mapStateToProps)(AppContainer);