import React, {useEffect, useRef} from 'react';
import {connect} from "react-redux";

import navSCSS from "./navSCSS.module.scss"
import appSCSS from "../../app/app.module.scss"

import Nav from "./nav";

import {setClose} from "../../redux-toolkit/nav/nav-slice";

const setActiveLink = ({isActive}) => isActive ? navSCSS.active : "";

const NavContainer = ({isOpen, setClose}) => {
    const refNav = useRef(null);

    const handleClickOutSide = (event) => {
        if (!refNav.current?.contains(event.target) && !document.querySelector(`img[alt="button nav"]`)?.contains(event.target)) {
            setClose();
        }
    }

    useEffect(() => {
        window.addEventListener("click", handleClickOutSide)
        return () => {
            window.removeEventListener("click", handleClickOutSide)
        }
    }, [])

    const handleClickClose = (event) => {
        if ((event.target.nodeName === "P") || (event.target.nodeName === "IMG") || (event.target.nodeName === "A")) {
            setClose()
        }
    }

    const handleResizeNav = () => {
        const windowInnerClientHeight = document.documentElement.clientHeight;

        const app_nav = document.getElementsByClassName(`${appSCSS.nav}`)[0]
        const app_nav_margin_top = +getComputedStyle(app_nav).marginTop.replace("px", "")
        const nav_grid = document.getElementsByClassName(`${navSCSS.grid}`)[0]
        nav_grid.style.height = `${windowInnerClientHeight - app_nav_margin_top}px`
    }

    useEffect(() => {
        handleResizeNav()
        window.addEventListener("resize", handleResizeNav)
        return () => {
            window.removeEventListener("resize", handleResizeNav)
        }
    }, [])

    return <Nav refNav={refNav} handleClickClose={handleClickClose} isOpen={isOpen} setActiveLink={setActiveLink}/>
}

const mapStateToProps = (state) => {
    return {
        isOpen: state.navSliceReducer.isOpen
    }
}

const mapDispatchToProps = {
    setClose
}

export default connect(mapStateToProps, mapDispatchToProps)(NavContainer);
