import React from 'react';

import {useMediaQuery} from "react-responsive";

import lessonsItemSCSS from "./lessons-itemSCSS.module.scss"

const LessonsItem = ({more, startTime, endTime, discipline, teacher, group, audience, typeOfLesson}) => {
    const is419 = useMediaQuery({query: `(max-width: 419px)`});
    const is1599 = useMediaQuery({query: `(max-width: 1599px)`});

    return <li className={lessonsItemSCSS.lessons_item}>
        <div className={lessonsItemSCSS.lessons_item_wrapper}>
            <div className={lessonsItemSCSS.time}>
                <p>{startTime}</p>
                <p>{endTime}</p>
            </div>
            <div className={lessonsItemSCSS.teacher_lesson}>
                <p className={lessonsItemSCSS.lesson}>{discipline}</p>
                <p className={lessonsItemSCSS.teacher + " " + ((is1599 && more) ? lessonsItemSCSS.active : more ? lessonsItemSCSS.deActive : lessonsItemSCSS.active)}>{teacher}</p>
            </div>
            {!is419 && <div>
                <p>{group}</p>
            </div>}
            <div>
                {is419 && <p>{group}</p>}
                <p>{audience}</p>
            </div>
            <div
                className={((is1599 && more) ? lessonsItemSCSS.deActive : more ? lessonsItemSCSS.active : lessonsItemSCSS.deActive)}>
                <p className={lessonsItemSCSS.teacher}>{teacher}</p>
            </div>
            <div
                className={lessonsItemSCSS.type_of_lesson + " " + (!more && lessonsItemSCSS.deActive)}>
                <p>{typeOfLesson}</p>
            </div>
        </div>
    </li>
}

export default LessonsItem;
