import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    currentPage: "/",
    pages: {
        main: "/",
        schedule: "/schedule",
        journal: "/journal",
        bactericidalLamp: "/bactericidal-lamp",
        students: "/students",
        teachers: "/teachers"
    },
    isOpen: false
};

const navSlice = createSlice({
    name: "navSlice",
    initialState,
    reducers: {
        setOpen: (state) => {
            state.isOpen = true
        },
        setClose: (state) => {
            state.isOpen = false
        },
        setCurrentPage: (state, action) => {
            state.current = action.payload
        }
    }
})

const {reducer, actions} = navSlice;

export const {setOpen, setClose, setCurrentPage} = actions;

export default reducer;