import React from 'react';

import lessonsSCSS from "./lessonsSCSS.module.scss";

const Lessons = ({more, children, handleTouchStart, handleTouchMove}) => {
    return <ul onTouchStart={more && handleTouchStart} onTouchMove={more && handleTouchMove}
               className={lessonsSCSS.lessons}>
        {children}
    </ul>
}

export default Lessons;
