import React from 'react';

import Search from "../forms/search/search-container";

import appSCSS from "../../app/app.module.scss"
import headerSCSS from "./headerSCSS.module.scss"

import imgBurgerMenu from "../../assets/images/burger menu.svg"
import imgCross from "../../assets/images/cross.svg"

const Header = ({titlePage, setOpen, setClose, isOpen}) => {
    return <div className={appSCSS.header + " " + appSCSS.bcg}>
        <div className={headerSCSS.grid}>
            <div className={headerSCSS.page_name}>
                {(!isOpen && <img onClick={setOpen} src={imgBurgerMenu} alt={"button nav"}/>) || (<img
                    onClick={setClose}
                    src={imgCross}
                    alt={"button nav"}/>)}
                <h1>{titlePage}</h1>
            </div>
            <Search/>
        </div>
    </div>
}

export default Header;
