import React from 'react';
import {connect} from "react-redux";

import Schedule from "./schedule";

function ScheduleContainer({showCountCurrent, show, pageCalendar}) {
    return <Schedule pageCalendar={pageCalendar} show={show}
                     showCountCurrent={showCountCurrent}/>
}

const mapStateToProps = (state) => {
    return {
        showCountCurrent: state.calendarSliceReducer.typeCalendar.full.showCount,
        show: state.calendarSliceReducer.showCount,
        pageCalendar: state.calendarSliceReducer.typeCalendar.full.pageCalendar
    }
}

export default connect(mapStateToProps)(ScheduleContainer);