import {createSlice} from "@reduxjs/toolkit";
import parityWeek from "../../help functions/parity-week-calendar";
import week_count_calendar from "../../help functions/week-count-calendar";
import number_of_the_week_by_date_calendar from "../../help functions/number-of-the-week-by-date-calendar";

const current_date = new Date();

const initialState = {
    currentFullDate: current_date,
    typeCalendar: {
        full: {
            pageCalendar: current_date,
            selectedFullDate: current_date,
            selectedDate: current_date.getDate(),
            selectedMonth: current_date.getMonth(),
            selectedFullYear: current_date.getFullYear(),
            selectedParity: parityWeek(current_date, current_date.getDate()),
            showCount: {
                current: {
                    number: 7,
                    numberWeek: number_of_the_week_by_date_calendar(current_date, current_date.getDate()),
                    text: ""
                }
            }
        },
        small: {
            pageCalendar: current_date,
            selectedFullDate: current_date,
            selectedParity: parityWeek(current_date, current_date.getDate())
        }
    },
    showCount: {
        week: {
            number: 7,
            numberWeek: 1,
            text: ""
        },
        two_week: {
            number: 14,
            numberWeek: 1,
            text: "2 "
        },
        month: {
            number: (week_count_calendar(current_date) * 7),
            text: ""
        }
    }
};

const calendarSlice = createSlice({
    name: "calendarSlice",
    initialState,
    reducers: {
        setSelectedFullDateSmall: (state, action) => {
            state.typeCalendar.small.selectedFullDate = action.payload;
            state.typeCalendar.small.selectedParity = parityWeek(state.typeCalendar.small.selectedFullDate, state.typeCalendar.small.selectedFullDate.getDate());
            state.showCount.month.number = week_count_calendar(state.typeCalendar.full.selectedFullDate);
        },
        setSelectedFullDate: (state, action) => {
            state.typeCalendar.full.selectedFullDate = action.payload;
            state.typeCalendar.full.selectedDate = action.payload.getDate();
            state.typeCalendar.full.selectedMonth = action.payload.getMonth();
            state.typeCalendar.full.selectedFullYear = action.payload.getFullYear();
            state.typeCalendar.full.selectedParity = parityWeek(state.typeCalendar.full.selectedFullDate, state.typeCalendar.full.selectedDate);
            state.showCount.month.number = week_count_calendar(state.typeCalendar.full.selectedFullDate);
            state.typeCalendar.full.showCount.current.numberWeek = number_of_the_week_by_date_calendar(action.payload, action.payload.getDate())
        },
        nextMonth: (state) => {
            state.typeCalendar.full.showCount.current.numberWeek = state.showCount.week.numberWeek;
            if (state.typeCalendar.full.pageCalendar.getMonth() === 11) {
                const nextMonth = new Date(state.typeCalendar.full.pageCalendar.getFullYear() + 1, 0, state.typeCalendar.full.selectedFullYear === state.typeCalendar.full.pageCalendar.getFullYear() + 1 && state.typeCalendar.full.selectedMonth === 0 ? state.typeCalendar.full.selectedDate : 1)
                calendarSlice.caseReducers.setSelectedFullDate(state, {
                    payload: nextMonth
                });
                state.typeCalendar.full.pageCalendar = nextMonth
            } else {
                const nextMonth = new Date(state.typeCalendar.full.pageCalendar.getFullYear(), state.typeCalendar.full.pageCalendar.getMonth() + 1, state.typeCalendar.full.selectedFullYear === state.typeCalendar.full.pageCalendar.getFullYear() && state.typeCalendar.full.selectedMonth === state.typeCalendar.full.pageCalendar.getMonth() + 1 ? state.typeCalendar.full.selectedDate : 1)
                calendarSlice.caseReducers.setSelectedFullDate(state, {
                    payload: nextMonth
                });
                state.typeCalendar.full.pageCalendar = nextMonth
            }
        },
        next: (state) => {
            if (state.typeCalendar.full.showCount.current.text === state.showCount.month.text) {
                calendarSlice.caseReducers.nextMonth(state);
            } else if (state.typeCalendar.full.showCount.current.text === state.showCount.week.text) {
                if (state.typeCalendar.full.showCount.current.numberWeek < week_count_calendar(state.typeCalendar.full.pageCalendar)) {
                    state.typeCalendar.full.showCount.current.numberWeek++;
                } else {
                    calendarSlice.caseReducers.nextMonth(state);
                }
            } else if (state.typeCalendar.full.showCount.current.text === state.showCount.two_week.text) {
                if (state.typeCalendar.full.showCount.current.numberWeek < (week_count_calendar(state.typeCalendar.full.pageCalendar) / 2 + (week_count_calendar(state.typeCalendar.full.pageCalendar) % 2 === 0 ? 2 : 1))) {
                    state.typeCalendar.full.showCount.current.numberWeek++;
                } else {
                    calendarSlice.caseReducers.nextMonth(state);
                }
            }
        },
        prevMonth: (state) => {
            state.typeCalendar.full.showCount.current.numberWeek = state.showCount.week.numberWeek;
            if (state.typeCalendar.full.pageCalendar.getMonth() === 0) {
                const prevMonth = new Date(state.typeCalendar.full.pageCalendar.getFullYear() - 1, 11, state.typeCalendar.full.selectedFullYear === state.typeCalendar.full.pageCalendar.getFullYear() - 1 && state.typeCalendar.full.selectedMonth === 11 ? state.typeCalendar.full.selectedDate : new Date(state.typeCalendar.full.pageCalendar.getFullYear() - 1, 0, 0).getDate())
                calendarSlice.caseReducers.setSelectedFullDate(state, {
                    payload: prevMonth
                });
                state.typeCalendar.full.pageCalendar = prevMonth
            } else {
                const prevMonth = new Date(state.typeCalendar.full.pageCalendar.getFullYear(), state.typeCalendar.full.pageCalendar.getMonth() - 1, state.typeCalendar.full.selectedFullYear === state.typeCalendar.full.pageCalendar.getFullYear() && state.typeCalendar.full.selectedMonth === state.typeCalendar.full.pageCalendar.getMonth() - 1 ? state.typeCalendar.full.selectedDate : new Date(state.typeCalendar.full.pageCalendar.getFullYear(), state.typeCalendar.full.pageCalendar.getMonth(), 0).getDate())
                calendarSlice.caseReducers.setSelectedFullDate(state, {
                    payload: prevMonth
                });
                state.typeCalendar.full.pageCalendar = prevMonth
            }
        },
        prev: (state) => {
            if (state.typeCalendar.full.showCount.current.text === state.showCount.month.text) {
                calendarSlice.caseReducers.prevMonth(state);
            } else if (state.typeCalendar.full.showCount.current.text === state.showCount.week.text || state.typeCalendar.full.showCount.current.text === state.showCount.two_week.text) {
                if (state.typeCalendar.full.showCount.current.numberWeek > 1) {
                    state.typeCalendar.full.showCount.current.numberWeek--;
                } else {
                    calendarSlice.caseReducers.prevMonth(state);
                }
            }
        },
        setNextDay: (state) => {
            const nextDay = new Date(state.typeCalendar.full.selectedFullYear, state.typeCalendar.full.selectedMonth, state.typeCalendar.full.selectedDate + 1)
            calendarSlice.caseReducers.setSelectedFullDate(state, {
                payload: nextDay
            });

            if (new Date(state.typeCalendar.full.pageCalendar.getFullYear(), state.typeCalendar.full.pageCalendar.getMonth() + 1, 1).getMonth() === state.typeCalendar.full.selectedMonth) {
                calendarSlice.caseReducers.nextMonth(state);
            }
        },
        setPrevDay: (state) => {
            const prevDay = new Date(state.typeCalendar.full.selectedFullYear, state.typeCalendar.full.selectedMonth, state.typeCalendar.full.selectedDate - 1)
            calendarSlice.caseReducers.setSelectedFullDate(state, {
                payload: prevDay
            });

            if (new Date(state.typeCalendar.full.pageCalendar.getFullYear(), state.typeCalendar.full.pageCalendar.getMonth() - 1,).getMonth() === state.typeCalendar.full.selectedMonth) {
                calendarSlice.caseReducers.prevMonth(state);
            }
        },
        setShowWeek: (state) => {
            state.typeCalendar.full.showCount.current.number = state.showCount.week.number;
            state.typeCalendar.full.showCount.current.text = state.showCount.week.text;
        },
        setShowTwoWeek: (state) => {
            state.typeCalendar.full.showCount.current.number = state.showCount.two_week.number;
            state.typeCalendar.full.showCount.current.text = state.showCount.two_week.text;
        },
        setShowMonth: (state) => {
            state.typeCalendar.full.showCount.current.number = state.showCount.month.number;
            state.typeCalendar.full.showCount.current.text = state.showCount.month.text;
        }
    }
})

const {reducer, actions} = calendarSlice;

export const {
    setSelectedFullDate,
    next,
    prev,
    setShowWeek,
    setShowTwoWeek,
    setShowMonth,
    setSelectedFullDateSmall,
    setNextDay,
    setPrevDay
} = actions;

export default reducer;