import React from 'react';
import {connect} from "react-redux";

import {setNextDay, setPrevDay} from "../../../redux-toolkit/calendar/calendar-slice";

import Lessons from "./lessons";

const LessonsContainer = ({more, children, setNextDay, setPrevDay}) => {
    let xDown = null;
    let yDown = null;

    const handleTouchStart = (event) => {
        const firstTouch = event.touches[0];
        xDown = firstTouch.clientX;
        yDown = firstTouch.clientY;
    }

    const handleTouchMove = (event) => {
        if (!xDown || !yDown) {
            return;
        }

        let xUp = event.touches[0].clientX;
        let yUp = event.touches[0].clientY;

        let xDiff = xDown - xUp;
        let yDiff = yDown - yUp;

        if (Math.abs(xDiff) > Math.abs(yDiff)) {/*most significant*/
            if (xDiff > 0) {
                /* right swipe */
                setNextDay()
            } else {
                /* left swipe */
                setPrevDay()
            }
        } else {
            if (yDiff > 0) {
                /* down swipe */
            } else {
                /* up swipe */
            }
        }

        xDown = null;
        yDown = null;
    }
    return <Lessons more={more} children={children} handleTouchStart={handleTouchStart}
                    handleTouchMove={handleTouchMove}/>
}

const mapDispatchToProps = {
    setNextDay,
    setPrevDay
}

export default connect(null, mapDispatchToProps)(LessonsContainer);
