import React from 'react';

import searchSCSS from "./searchSCSS.module.scss"
import headerSCSS from "../../header/headerSCSS.module.scss";

import imgSearch from "../../../assets/images/search/search.svg";
import imgCross from "../../../assets/images/cross.svg";

const Search = ({isInput, handleKeyPress, handleClickCross, handleChangeValue, handleSubmit}) => {
    return <form onSubmit={handleSubmit} className={[searchSCSS.search, headerSCSS.search].join(" ")}>
        <img src={imgSearch} alt={"search"}/>
        <input onKeyDown={handleKeyPress} onChange={handleChangeValue} value={isInput} type={"text"}
               placeholder={""}/>
        {isInput && <img className={searchSCSS.cross} onClick={handleClickCross} src={imgCross} alt={"cross"}/>}
    </form>
}

export default Search;
